import {Component, ChangeDetectionStrategy, ChangeDetectorRef, Output, EventEmitter} from '@angular/core';

@Component({
  selector: 'ngl-pill',
  template: `
    <ng-content select="[nglPillImage]"></ng-content><ng-content select="a"></ng-content><span class="slds-pill__label" *ngIf="unlinked"><ng-content></ng-content></span><button class="slds-button slds-button--icon slds-pill__remove" *ngIf="removable" type="button" (click)="remove()"><svg class="slds-button__icon" nglIcon="close"></svg></button>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.slds-pill]': 'true',
  },
})
export class NglPill {
  removable: boolean;
  unlinked: boolean = true;

  @Output() nglPillRemove = new EventEmitter();

  constructor(public detector: ChangeDetectorRef) {}

  remove() {
    this.nglPillRemove.emit(null);
  }
}
